import { Component, OnInit, Input, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-view-vendor-report',
  templateUrl: './view-vendor-report.component.html',
  styleUrls: ['./view-vendor-report.component.scss']
})
export class ViewVendorReportComponent implements OnInit {

  @Input() vendor:any;
  constructor(
    public dialogRef: MatDialogRef<ViewVendorReportComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
      if(this.data){
        this.vendor = this.data;
      }
    }

  ngOnInit(): void {
  }

}
